@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
body {
  background-color: rgb(188, 214, 214);
  background-repeat: no-repeat;
  background-image: url("../public/Subtract.jpg");
  background-position: right top;
  font-family: "Poppins", sans-serif;
}
/* customize scroll bar */
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 255, 0.705);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.active,
.logout {
  color: white;
  background-color: rgb(58, 58, 197);
  font-weight: bolder;
  padding: 5px 10px;
  border-radius: 50px;
  box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.5);
}
task-container {
  height: 90vh;
  background-color: whitesmoke;
}

.addTask {
  height: 10vh;
  background-color: transparent;
  padding: 10px;
}
.outlet {
  height: 90vh;
  background-color: whitesmoke;
  border-radius: 20px;
  padding: 1rem;
  overflow: auto;
}
.indicator {
  display: flex;
  align-items: center;
  height: 10vh;
  margin: auto 10px;
}
